import TrafficFlow from './components/TrafficFlow/TrafficFlow'

const components = [
  TrafficFlow
  // ...
]

const install = function (Vue, opts = {}) {
  components.map(component => {
    Vue.component(component.name, component)
  })
}

/*  */
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export default {
  install,
  TrafficFlow
  // ...
}
